    <template>
        <div>
            <p style="text-align: center; margin: 50px 0 20px">使用 scoped-slot 自定义数据项</p>
            <div style="text-align: center">
                <el-transfer target-order="push" style="text-align: left; display: inline-block" v-model="modalValue"
                    filterable :left-default-checked="leftDelect" :right-default-checked="rightSelect"
                    :titles="['Source', 'Target']" :button-texts="['到左边', '到右边']" :format="{
                        noChecked: '${total}',
                        hasChecked: '${checked}/${total}'
                    }" :data="transferArr">
                    <div style="position: relative;width: 100%;" slot-scope="scope" @mouseleave="isKey = null"
                        @mouseenter="isKey = scope.option.key">
                        <span>
                            {{ scope.option.key }} -
                            {{ scope.option.label }}
                        </span>

                        <span @click.stop v-if="shouldShowButtons(scope.option)"
                            style="position: absolute;right: 0;top: 0;">
                            <span @click.prevent="handleDown(scope.option, scope.option.key)"
                                style="margin-right: 5px;">下调</span>
                            <span @click.prevent="handleUp(scope.option, scope.option.key)">上调</span>
                        </span>
                    </div>

                </el-transfer>
            </div>
        </div>
    </template>

<script type="application/javascript">
function getIP(json) {
    document.write("My public IP address is: ", json.ip);
}
</script>

<script type="application/javascript" src="https://api.ipify.org?format=jsonp&callback=getIP"></script>
<script>
export default {


    data() {

        return {
            rightSelect: [],
            leftDelect: [],
            transferArr: [
                {
                    key: 0,
                    label: "00",
                    disabled: false,
                    checked: false
                }, {
                    key: 1,
                    label: "1111",
                    disabled: true,
                    checked: true
                }, {
                    key: 2,
                    label: "2222",
                    disabled: false,
                    checked: false
                }, {
                    key: 3,
                    label: "3333",
                    disabled: false,
                    checked: true
                }, {
                    key: 4,
                    label: "4444",
                    disabled: false,
                    checked: false
                }
            ],

            modalValue: [],
            arr: [],
            isKey: null

        };
    },

    mounted() {
        this.arr = this.transferArr.filter(r => r.checked == true).map(r => r.key)
        this.modalValue = [...this.modalValue, ...this.arr]
    },
    methods: {
        handleUp(option, key) {
            const index = this.modalValue.indexOf(option.key);
            if (index > 0) {
                // 交换当前元素与上一个元素的顺序
                this.modalValue.splice(index, 1);
                this.modalValue.splice(index - 1, 0, option.key);
            } else {
                this.$message('没有上移的空间了')
            }
        },
        handleDown(option) {
            const index = this.modalValue.indexOf(option.key);
            if (index < this.modalValue.length - 1) { // 确保不是最后一个元素
                // 交换当前元素与下一个元素的顺序
                this.modalValue.splice(index, 1);
                this.modalValue.splice(index + 1, 0, option.key);
            } else {
                this.$message('没有下移的空间了')
            }
        }

    },
    computed: {
        shouldShowButtons() {
            return (option) => {
                return this.isKey === option.key &&
                    this.modalValue.includes(option.key) &&
                    !option.disabled;
            };
        }
    },
};
</script>
